<script>
	import { onMount } from 'svelte';
	import { PATH_BLOG } from './lib/const.js';
	import Get from './lib/net.js';
	import marked from 'marked';

	export let title;
	export let location;
	let content = "";
	
	onMount(async function(){
		let res = await Get(PATH_BLOG + "/" + title);
		res = await res.text();
		content = marked(res);
	})

</script>
<div class="card-content" contenteditable="true" bind:innerHTML={content} readonly/>
<style>
	@import url("/css/sspai.css");
</style>
